.elmloading{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0rem;
        left: 0rem;
        padding-top: 23rem;
}
    /*图案运动*/
.imgbox{
        width: 5.5rem;
        height: 5.5rem;
        margin: 0rem auto;
        -webkit-animation: myfirst 0.7s infinite;
                animation: myfirst 0.7s infinite;
}
@-webkit-keyframes myfirst
    {
0% {margin-top:0rem;
}
50% {margin-top:5rem;
}
100% {margin-top:0rem;
}
}
@keyframes myfirst
    {
0% {margin-top:0rem;
}
50% {margin-top:5rem;
}
100% {margin-top:0rem;
}
}
    /*动画图案*/
.imgbackground{
        width: 100%;
        height: 100%;
        background: url(data:image/png;base64,) no-repeat;
        background-size: 100%;
        -webkit-animation: myf 4.9s infinite;
                animation: myf 4.9s infinite;
}
@-webkit-keyframes myf
    {
0% {background-position:0% 0%;
}
14.2% {background-position:0% 0%;
}
14.2001% {background-position:0% 16%;
}
28.4% {background-position:0% 16%;
}
28.4001% {background-position:0% 33%;
}
42.6% {background-position:0% 33%;
}
42.6001% {background-position:0% 50%;
}
56.8% {background-position:0% 50%;
}
56.8001% {background-position:0% 67%;
}
71% {background-position:0% 67%;
}
71.0001% {background-position:0% 84%;
}
85.2% {background-position:0% 84%;
}
85.2001% {background-position:0% 100%;
}
100% {background-position:0% 100%;
}
}
@keyframes myf
    {
0% {background-position:0% 0%;
}
14.2% {background-position:0% 0%;
}
14.2001% {background-position:0% 16%;
}
28.4% {background-position:0% 16%;
}
28.4001% {background-position:0% 33%;
}
42.6% {background-position:0% 33%;
}
