}
85.2001% {background-position:0% 100%;
}
100% {background-position:0% 100%;
}
}
    /*动画阴影*/
.loadfoot{
        width: 100%;
        height: 5rem;
        text-align: center;
        position: absolute;
        top: 34rem;
}
.footspan{
        height: 2rem;
        width: 6rem;
        display: inline-block;
        border-radius: 50%;
        background: radial-gradient(#7E7E7E, #EDEDED);
        opacity: 0.5;
        -webkit-animation: foot 0.7s infinite;
                animation: foot 0.7s infinite;
}
@-webkit-keyframes foot
    {
0% {width: 6rem;height: 2rem;
}
50% {width: 3rem;height: 1rem;
}
100% {width: 6rem;height: 2rem;
}
}
@keyframes foot
    {
0% {width: 6rem;height: 2rem;
}
50% {width: 3rem;height: 1rem;
}
100% {width: 6rem;height: 2rem;
}
}
