    /*vant组件样式*/
.detail_top div{
    background: #3190e8;
    color: white;
    font-size: 1.7rem;
    font-weight: bolder;
    height: 4rem;
    line-height: 4rem;
}
.detail_top i{
    color: white!important;
    font-size: 2.5rem;
    line-height: 4rem;
}
    /*弹窗动画*/
.tan-leave-active{
        animation: bounceIn 0.5s linear reverse;
}
.tan-enter-active{
        animation: bounceIn 0.5s linear reverse;
}
