    /*弹窗*/
.tanchuang{
      position: absolute;
      width: 70%;
      border: none;
      background: white;
      text-align: center;
      top: 20rem;
      border-radius: 0.5rem;
      left: 6rem;
}
.tanchuang button{
      width: 100%;
      height: 4rem;
      background: #4cd964;
      border: none;
      font-size: 1.7rem;
      color: white;
      border-radius: 0rem 0rem 0.5rem 0.5rem;
}
.tanchuang i{
      color: #f8cb86;
      font-size: 8rem;
      margin: 1rem;
}
.tanchuang span{
      display: inline-block;
      font-size: 1.5rem;
      margin-bottom: 1rem;
}












