html,
body {
  width: 100%;

}

body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  color: #595757;
  font-family: 'PingFang SC', 'STHeitiSC-Light', "Microsoft YaHei", Arial, "Helvetica Neue", Helvetica, sans-serif;
  /* 所有元素默认大小 */
  font-size: 16px;
}

@media only screen and (min-width: 320px) {
  html {
    /* font-size: 12px !important; */
  }
}

@media only screen and (min-width: 400px) {
  html {
    /* font-size: 18px !important; */
  }
}

@media only screen and (min-width: 481px) {
  html {
    /* font-size: 20px !important;  */
  }
}

@media only screen and (min-width: 569px) {
  html {
    /* font-size: 22px !important;  */
  }
}

@media only screen and (min-width: 641px) {
  html {
    /* font-size: 24px !important;  */
  }
}

/* 以6s（375物理像素高清屏） 750px尺寸为基准：参考与是750px，

16px/(375/750) = ?px/(320屏幕尺寸/750) ->  ? = 32*(320/750) = 13.65333px;

 */

@media only screen and (min-width: 736px) {
  html {
    font-size: 31.36px;
  }
}

@media only screen and (min-width: 415px) and (max-width: 735px) {
  html {
    font-size: 17.664px;
  }
}

/* 基准------------------- */

@media only screen and (min-width: 375px) and (max-width: 414px) {
  html {
    font-size: 100px;
  }
}

@media only screen and (max-width: 374px) {
  html {
    font-size: 13.65333px;
  }
}

input[type=button],
input[type=submit],
input[type=file],
button {
  cursor: pointer;
  -webkit-appearance: none;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.left {
  float: left;
}

.right {
  float: right;
}

.root-color {
  color: #90B4FC;
}

.f0 {
  font-size: 0;
}

.hidden {
  display: none;
}

/*垂直居中*/

.baseline:before {
  content: "";
  width: 0px;
  visibility: hidden;

  display: inline-block;
  height: 100%;
  background: gray;
  vertical-align: middle;
}
